<template>
<div class="my-5">
    <h3 class="display-4">Todolist: </h3>
    <div class="todos">
        <div class="todo" v-for="todo in allTodos" :key="todo.id">
            {{ todo.title }}
            <font-awesome-icon icon="trash" class="delIcon" @click="delTodo" />
        </div>
    </div>
</div>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
export default {
    name: "Todos",
    computed: mapGetters(['allTodos']),
    methods: {
        ... mapActions(['getTodos', 'delTodo'])
        },
    created(){
        this.getTodos()
    }
}
</script>

<style scoped>
.todos {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 1rem;
    column-gap: 1rem;
}

.todo {
    position: relative;
    border: 1px solid #333;
    background-color: aqua;
    border-radius: 3px;
    padding: 1rem 0.5rem;
    cursor: pointer;
}

.delIcon{
    position: absolute;
    right: 10px;
    top: 20px;
}
</style>