<template>
  <div class="add-form">
       <div class="container">
      <form @submit="formSubmit" class="my-5">
          <input type="text" class="form-control mb-2" placeholder="Post Title" v-model="title">
          <textarea class="form-control mb-2"  rows="10" placeholder="Post Body" v-model="body"></textarea>
          <button type="submit" class="btn btn-success">Submit</button>

      </form>
  </div>
  </div>
</template>

<script>
import { mapActions } from "vuex"
export default {
    name: "AddPost",
    data() {
        return {
            title: "",
            body: ""
        }
    },
    methods: {
        ...mapActions(["send_post"]),
        formSubmit(e) {
            e.preventDefault();
            let data = {
                title: this.title,
                body: this.body
            }
            this.send_post(data)
            this.title = ""
            this.body = ""
        }
    }
}
</script>

