<template>
  <div class="posts-list">
    <div class="container">
        <h2 class="display-4 my-5 text-center">All Posts:</h2>

      <!-- GET DATA WHEN CLICK ON BTN -->
      <!-- <button @click="handlePosts">Get</button> -->
      <div class="posts">
        <div class="blog-post" v-for="post in allPosts" :key="post.id">
            <h2>{{ post.title }}</h2>
            <p>{{ post.body }}</p>
            <i class="fas fa-trash delete" @click="delPost"></i>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex"
export default {
    name :"PostsList",
    computed : mapGetters(["allPosts"]),
    filters: {
      upperCase(value) {
        return value.toUpperCase()
      }
    },
    methods: {
      ...mapActions(["getPosts", "delPost"]),

    }
    ,
    // GET DATA BEFORE CMP CREATED
    created() {
        this.getPosts()
      }
}
</script>

<style scoped>
.blog-post {
  background-color: #d9d9d9;
  border-radius: 5px;
  padding: 10px;
  margin: 10px;
  position: relative;
}

.delete{
  position: absolute;
  right: 10px;
  bottom: 5px;
  cursor: pointer;
}
</style>