<template>
  <div class="addTodo my-5">
      <form @submit="formSubmit">
          <input type="text" class="form-control mb-2" v-model="title">
          <button class="btn btn-danger">Add</button>
      </form>
  </div>
</template>

<script>
import { mapActions } from "vuex"
export default {
    name: "addTodo",
    data() {
        return {
            title: ""
        }
    },
    methods: {
        ...mapActions(["postTodo"]),
        formSubmit(e){
            e.preventDefault();
            this.postTodo(this.title)
            this.title = "";
        }
    }
}
</script>

